.textfield, .select { background-color: #ffffff; display: block; margin-top: -2px; margin-bottom: 2.4rem; position: relative; }

.textfield__label, .select__label { font-size: 1.2rem; position: absolute; left: 1px; line-height: 1; padding: .5rem; right: 1px; top: 1px; }

.textfield input, .textfield textarea, .select select { background-color: transparent; border: 1px solid #7d7d7e; border-radius: 2px; -webkit-box-sizing: border-box; box-sizing: border-box; padding: 1.8rem .5rem .6rem; position: relative; width: 100%; z-index: 100; }

.textfield *:focus, .select select:focus { border-color: #E83FB8; -webkit-box-shadow: 0 1px 2px 0 #dededf inset; box-shadow: 0 1px 2px 0 #dededf inset; outline: none; }




