@import url('https://fonts.googleapis.com/css?family=Lobster');

.App__header {
  text-align: center;
}

.App__header small {
  font-size: inherit;
  color: #000;
  font-family: "Lobster", sans-serif;
  display: block;
}

.App label {
  margin-top: 1rem;
  margin-bottom: 0;
}

.App label:first-child {
  margin-top: 0;
}

.column--full {
  width: 100%;
}

.grid {
  -ms-flex-align: center;
      align-items: center;
  margin-bottom: 2.4rem;
  min-height: 150px;
}

.textfield--error {
  border: 1px solid #ff4136;
}



